footer {
    height: 8vh;
    background-image: linear-gradient(#0C0E13, #0C0E13, #0C0E13);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1em;
    border-top: 1px solid #454751;
    color: white;
    padding: 0;
}

main p, footer p {
    width: 100%;
    text-align: center;
    font-size: 1em;
}